.support{
  display: block;
  position: relative;
  float: left;
  width: 100%;
  box-shadow: 0 20px 30px rgba(0, 0, 0, 0.1);
  &__title{
    text-align: center;
    padding-top: 10px;
  }
  &__blocks{
    height: 100px;
    width: 900px;
    margin: 0 auto;
    padding: 20px 0;
    box-sizing: border-box;
  }
  &__block {
    display: inline-block;
    width: 31.3%;
    float: left;
    padding: 0 1%;
    box-sizing: border-box;
    &:last-of-type {
    @include breakpoint(max-width 668px) {
      width: 100%;
    }
  }
    @include breakpoint(max-width 668px) {
      width: 50%;
    }
    img{
      width: 30%;
      float: left;
    }
    p{
      padding: 1%;
      width: 68%;
      float: left;
    }
  }
}